import React, { Component } from "react";
import io from "socket.io-client";
class Chat extends Component {
  constructor(props) {
    super(props);
    this.state = {
      io: null,
      //   endpoint: "http://localhost:4000",
      endpoint: "https://soundersocket.herokuapp.com",
      message: "",
      recieve: [],
      name: "",
      stream: null
    };
  }
  componentDidMount = () => {
    const { endpoint } = this.state;
    let socket = io(endpoint);
    this.setState({
      io: socket
    });

    let name = prompt("Enter Your Name Please!");
    this.setState({
      name: name
    });
    socket.on("chat-message", message => {
      this.setState(state => {
        state.recieve.push(message);
        return {
          recieve: state.recieve
        };
      });
    });
  };
  onTyping = e => {
    let { io } = this.state;
    io.emit("onTyping");
    this.setState({
      message: e.target.value
    });
  };
  onSubmit = async e => {
    let { io, message, name } = this.state;
    let send = {
      id: io.id,
      message: message,
      name: name
    };
    io.emit("message", send);

    this.setState(state => {
      state.recieve.push(send);
      return {
        message: "",
        recieve: state.recieve
      };
    });
  };
  render() {
    const messsages = this.state.recieve.map((v, k) => {
      return (
        <div
          className={`col-12 ${
            v.id === this.state.io.id
              ? "justify-content-end"
              : "justify-content-start py-1"
          }`}
          key={k}
        >
          <div
            className={`DpTime ${
              v.id !== this.state.io.id ? "d-flex" : "d-none"
            }`}
          >
            <div className="dp">
              <img src="./img/msg.png" alt="" />
            </div>
            <span>1:03 AM</span>
          </div>
          <div className="nameMsg">
            <span className="Name">{v.name}</span>
            <div
              className={`Msg ${
                v.id === this.state.io.id ? "bg-light text-dark" : ""
              }`}
            >
              {v.message}
            </div>
          </div>
        </div>
      );
    });
    return (
      <React.Fragment>
        <div className="container MsgContainer">
          <div className="row">
            <div className="Messages col-12">
              <div className="container">
                <div className="row firstHead">
                  <span className="Heading">Halena James</span>
                  <span className="lastSeen">Last seen 7h ago</span>
                  <hr />
                  <div className="Bar">
                    <hr />
                    <p className="ml-2 mr-1">New</p>
                    <p className="mr-2">Message</p>
                    <hr />
                  </div>
                </div>
                <div className="row">{messsages}</div>
              </div>
            </div>
            <div className="Input col-12">
              <input
                type="text"
                placeholder="Enter Message...."
                onChange={this.onTyping}
                value={this.state.message}
              />
              <button onClick={this.onSubmit}>
                <img src="./img/play.png" alt="" />
              </button>
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Chat;
