import React, { Component } from 'react';
class Inbox extends Component {
    constructor(props) {
        super(props);
        this.state = {
            Inbox: [
                {
                    img: "./img/msg.png",
                    name: "Mosh Hamdani",
                    msg: "Hello Word",
                    time: "1:03 PM"
                },
                {
                    img: "./img/SubAccount/1.png",
                    name: "Helena Fischer",
                    msg: "Voice message..",
                    time: "4:03 AM"
                },
                {
                    img: "./img/SubAccount/2.png",
                    name: "Kayla Anderson",
                    msg: "Great!",
                    time: "12:03 AM"
                },
                {
                    img: "./img/msg.png",
                    name: "Mosh Hamdani",
                    msg: "Hello Word",
                    time: "1:03 PM"
                },
                {
                    img: "./img/SubAccount/1.png",
                    name: "Helena Fischer",
                    msg: "Voice message..",
                    time: "4:03 AM"
                },
                {
                    img: "./img/SubAccount/2.png",
                    name: "Kayla Anderson",
                    msg: "Great!",
                    time: "12:03 AM"
                }
            ], temp: []
        };
    }
    componentDidMount = () => {
        this.setState({
            temp: this.state.Inbox
        })
    }
    onSearch = e => {
        let search = e.target.value;
        search = search.toLowerCase();
        if (search !== null && search !== "" && search !== undefined) {
            let temp = this.state.temp;
            for (let i = 0; i < temp.length; i++) {
                temp[i].name = temp[i].name.toLowerCase();
                let arr = temp.filter(x => x.name.includes(search) !== false);
                if (arr.length === 0) {
                    this.setState({
                        Inbox: arr
                    })
                }
                else {
                    let statearr = [];
                    for (let j = 0; j < arr.length; j++) {
                        statearr.push(arr[j]);
                        this.setState({
                            Inbox: statearr
                        })
                    }
                }
            }
        }
        else {
            this.setState({
                Inbox: this.state.temp
            })
        }
    }
    render() {
        console.log(this.state)
        const Inbox = this.state.Inbox.map((v, k) => {
            return (
                <React.Fragment key={k}>
                    <div className="col-12 msg">
                        <div className="dp">
                            <img src={v.img} alt="" />
                        </div>
                        <div className="title d-md-block d-none">
                            <span>{v.name}o</span>
                            <p className="msg">{v.msg}</p>
                        </div>
                        <div className="time d-md-block d-none">
                            <span>{v.time}</span>
                        </div>
                    </div>
                </React.Fragment>
            )
        })
        return (
            <React.Fragment>
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-12 heading d-md-flex d-none">
                            <span>Inbox</span>
                        </div>
                        <div className="col-12 Search">
                            <i className="fas fa-search"></i>
                            <input type="text" placeholder="Search...." onChange={this.onSearch} />
                        </div>
                        {Inbox}
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default Inbox;