import React, { Component } from 'react';
class Friends extends Component {
    constructor(props) {
        super(props);
        this.state = {
            Friends: [
                {
                    name: "Al Pachino",
                    img: "msg.png"
                },
                {
                    name: "Alezay Josh",
                    img: "/SubAccount/1.png"
                },
                {
                    name: "Sarena Morgan",
                    img: "/SubAccount/2.png"
                },
                
            ]
        };
    }
    render() {
        const Friends = this.state.Friends.map((v, k) => {
            return (
                <div className="col-12 user" key={k}>
                    <div className="dp">
                        <img src={`img/${v.img}`} alt="" />
                    </div>
                    <div className="name">
                        <p>{v.name}</p>
                    </div>
                    <div className="Icons">
                        <img src="./img/icon/msg.png" alt="" />
                        <img src="./img/icon/clock.png" alt="" />
                    </div>
                </div>
            )
        })
        return (
            <React.Fragment>
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-12 heading">
                            <span>Friends</span>
                        </div>
                        {Friends}
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default Friends;